*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0rem 0rem;
}

h2 {
  margin-block-start: 0rem;
  margin-block-end: 0rem;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 0;
  margin-block-end: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.7rem;
  background-color: #fcfcfc;
  color: #aaaaaa;
  position: relative;
  overflow-x: hidden;
}

.container-grid {
  padding-top: 7rem;
  max-width: 90vw;
  margin: 0 auto;
  display: grid;
  grid-column: 100%;
  grid-template-rows: calc(100vh - 7rem);
  row-gap: 10rem;
}

::selection {
  background-color: #25d7ff;
  color: #fcfcfc;
}

.heading {
  font-family: "Bebas Neue", cursive;
  color: #272727;
}
.heading--primary {
  font-size: 7rem;
}
.heading--secondary {
  font-size: 5rem;
}

.paragraph {
  line-height: 1.7;
}

.u-mt-s {
  margin-top: 1.5rem !important;
}

.u-mb-m {
  margin-bottom: 3rem !important;
}

.u-ta-c {
  text-align: center !important;
}

.btn:link, .btn:visited {
  font-size: 2rem;
  font-family: "Bebas Neue", cursive;
  letter-spacing: 1px;
  text-decoration: none;
  background-color: #fcfcfc;
  border: 2px solid #00a8cc;
  padding: 1rem 4rem;
  border-radius: 10rem;
  color: #00a8cc;
  transition: all 0.4s;
}
.btn:hover {
  background-color: #00a8cc;
  color: #fcfcfc;
}

.logo {
  height: 2.5rem;
}
.logo__img {
  height: 100%;
}

.options__link:link, .options__link:visited {
  text-decoration: none;
  color: inherit;
}
.options__option {
  cursor: pointer;
  display: grid;
  grid-template-columns: min-content 1fr;
  padding: 2rem 4rem;
  gap: 1rem 6rem;
  position: relative;
  transition: all 0.3s;
}
.options__option:hover {
  background-color: rgba(37, 215, 255, 0.1);
}
.options__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.options__box-img {
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  overflow: hidden;
  grid-row: 1/3;
  grid-column: 1/2;
}
.options__paragraph {
  grid-column: 2/3;
  grid-row: 2/span 1;
}

.puppies-options {
  margin-top: 5rem;
}
.puppies-options__select {
  margin-bottom: 6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.puppies-options__descrip {
  color: #00a8cc;
  margin-left: -9vw;
  font-size: 1.2rem;
  font-weight: 600;
}
.puppies-options__menu-options {
  text-transform: uppercase;
  appearance: none;
  outline: 0;
  width: 20vw;
  border: 1px solid #aaaaaa;
  background: #fcfcfc;
  background-image: none;
  flex: 1;
  padding: 0 0.5em;
  color: #aaaaaa;
  cursor: pointer;
  font-size: inherit;
  font-family: inherit;
  padding: 1rem 2rem;
  border-radius: 10rem;
  position: relative;
}
.puppies-options__main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 5rem;
}
.puppies-options__puppy {
  position: relative;
}
.puppies-options__puppy-box {
  width: 30rem;
  height: 35rem;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.2);
}
.puppies-options__puppy-box:hover {
  cursor: pointer;
  transition: all 0.35s;
}
.puppies-options__puppy-box:hover + .puppies-options__puppy-info {
  opacity: 0;
  visibility: hidden;
}
.puppies-options__puppy-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: all 0.35s ease;
}
.puppies-options__puppy-img:hover {
  transform: scale(1.2);
}
.puppies-options__puppy-info {
  position: absolute;
  bottom: 0;
  height: 45%;
  width: 100%;
  background-color: rgba(252, 252, 252, 0.7);
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  visibility: visible;
  opacity: 1;
  transition: all 0.3s;
}
.puppies-options__paragraph {
  font-weight: 600;
}
.puppies-options__paragraph span {
  color: #272727;
}
.puppies-options__paragraph--male {
  color: #00a8cc;
}
.puppies-options__paragraph--female {
  color: #af0057;
}

.quote {
  background-color: #272727;
  display: flex;
  width: 69rem;
  border-radius: 8px;
  gap: 3rem;
  padding: 2rem 4rem;
}
.quote__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.quote__box-img {
  width: 21rem;
  height: 10rem;
  border-radius: 10rem;
  overflow: hidden;
}
.quote__text {
  color: #fcfcfc;
  line-height: 1.6;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  font-size: 1.5rem;
}
.quote__author {
  text-transform: uppercase;
  font-style: italic;
  font-weight: 300;
  position: absolute;
  bottom: 1rem;
  right: 5rem;
}

.review {
  width: 90vw;
}
.review__container {
  display: flex;
}
.review__person {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}
.review__box-img {
  width: 11rem;
  height: 11rem;
  border-radius: 10rem;
  overflow: hidden;
  transform: translateY(50%);
  z-index: 100;
}
.review__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.review__comment {
  color: #fcfcfc;
  display: block;
  background-color: #00a8cc;
  padding: 7rem 12rem;
  height: 20rem;
  width: calc(90vw / 2 - 9px);
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.2);
  position: relative;
  line-height: 1.15;
}
.review__comment::after {
  content: '"';
  width: auto;
  height: auto;
  font-size: 38rem;
  position: absolute;
  top: -4.5rem;
  left: -0.5rem;
  font-family: "Catamaran", sans-serif;
  color: rgba(252, 252, 252, 0.5);
}

.footer {
  background-color: #00a8cc;
  height: 25vh;
  color: #fcfcfc;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr min-content;
  margin-top: 10rem;
  align-items: center;
  padding: 1.5rem 5vw;
}
.footer__box-logo {
  grid-row: 1/-1;
  grid-column: 2/3;
  justify-self: end;
}
.footer__img {
  height: 4rem;
}
.footer__fare {
  font-family: "Bebas Neue", cursive;
  font-size: 3rem;
}
.footer__copy {
  font-size: 1.5rem;
  justify-self: center;
  grid-column: 1/-1;
}

.header {
  height: 7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 5vw;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.2);
  z-index: 100000;
  background-color: #fcfcfc;
  position: fixed;
  width: 100%;
}

.navigation__menu {
  position: relative;
}
@media only screen and (max-width: 68.125em) {
  .navigation__menu {
    display: none;
  }
}
.navigation__list {
  display: flex;
  list-style: none;
  column-gap: 3rem;
}
.navigation__item {
  font-family: "Bebas Neue", cursive;
  font-size: 2rem;
}
.navigation__link:link, .navigation__link:visited {
  text-decoration: none;
  color: #272727;
  transition: all 0.3s;
}
.navigation__link:hover {
  color: #00a8cc;
}
.navigation__toggle {
  display: none;
  font-size: 3.5rem;
  position: absolute;
  right: 5vw;
  top: 1.5rem;
  color: #272727;
  padding: 5px;
  cursor: pointer;
}
@media only screen and (max-width: 68.125em) {
  .navigation__toggle {
    display: block;
  }
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
@media only screen and (max-width: 68.125em) {
  .hero {
    flex-direction: column;
    justify-content: center;
    padding-top: 10rem;
  }
}
.hero__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 1rem;
}
@media only screen and (max-width: 68.125em) {
  .hero__info {
    align-items: center;
    text-align: center;
  }
}
.hero__paragraph {
  width: 50rem;
}
.hero__img {
  flex: 0 0 50%;
  position: relative;
  height: 100%;
}
@media only screen and (max-width: 68.125em) {
  .hero__img {
    flex: 0 0 40%;
    width: 100%;
  }
}
.hero__imagen {
  width: 50rem;
  z-index: 10;
  position: absolute;
  right: 0;
  top: 0;
}
@media only screen and (max-width: 81.25em) {
  .hero__imagen {
    width: 40rem;
  }
}
@media only screen and (max-width: 68.125em) {
  .hero__imagen {
    width: 27rem;
    top: -5rem;
  }
}

.section-about {
  display: flex;
  column-gap: 13%;
  position: relative;
  padding: 5rem 0rem;
}
.section-about__img {
  flex: 0 0 30vw;
  height: 35vw;
  background-image: url(./about-us.jpg);
  background-position: 50% 82%;
  background-repeat: no-repeat;
  background-size: 130%;
  border-radius: 8px;
}
.section-about__quote {
  position: absolute;
  bottom: -7rem;
  left: 20rem;
}
.section-about__text {
  flex: 0 0 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin-top: -6rem;
}
.section-about__text--p {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.section-information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
}
.section-information__contain {
  width: 50vw;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.2);
  border-radius: 8px;
  overflow: hidden;
}
.section-information__title {
  display: block;
  color: #fcfcfc;
  background-color: #00a8cc;
  font-size: 2rem;
  font-family: "Bebas Neue", cursive;
  padding: 2.5rem 2rem;
}

.section-puppies {
  padding-top: 10rem;
}

.section-review {
  margin-top: 10rem;
}

.section-contact {
  padding: 8rem 0rem;
}
.section-contact__main {
  display: flex;
  margin-top: 8rem;
}
.section-contact__map {
  flex: 0 0 60rem;
  height: 55rem;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.2);
}
.section-contact__info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-contact__p {
  display: flex;
  align-items: center;
  font-weight: 600;
  text-decoration: none;
  color: inherit;
}
.section-contact__p:first-child {
  margin-bottom: 1rem;
}
.section-contact__p--wt {
  position: relative;
}
.section-contact__p--wt::after {
  transition: all 0.3s;
}
.section-contact__p--wt::before {
  content: "";
  transition: all 0.3s;
}
.section-contact__p--wt:hover::before {
  clip-path: polygon(49% 22%, 0% 100%, 100% 100%);
  background-color: #272727;
  height: 4rem;
  width: 3rem;
  position: absolute;
  bottom: -2.5rem;
}
.section-contact__p--wt:hover::after {
  content: "Selecciona para que nos puedas contactar!";
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fcfcfc;
  width: 100%;
  height: 8rem;
  background-color: #272727;
  position: absolute;
  bottom: -9rem;
  border-radius: 8px;
}
.section-contact__ico {
  font-size: 4rem;
  color: #00a8cc;
  transition: all 0.3s;
}

.section-contact .section-contact__info .section-contact__p:hover .section-contact__ico {
  color: #25d7ff;
}

.bg {
  width: 90rem;
  height: 90rem;
  background-color: #00a8cc;
  border-radius: 100rem;
  position: absolute;
  right: 0;
  transform: translate(13rem, -9rem);
  z-index: -1;
}
@media only screen and (max-width: 117.1875em) {
  .bg {
    transform: translate(15rem, -10rem);
  }
}
@media only screen and (max-width: 85.625em) {
  .bg {
    width: 80rem;
    height: 80rem;
    transform: translate(10rem, 0rem);
  }
}
@media only screen and (max-width: 81.25em) {
  .bg {
    width: 70rem;
    height: 70rem;
    transform: translate(15rem, 0rem);
  }
}
@media only screen and (max-width: 68.125em) {
  .bg {
    width: 40rem;
    height: 40rem;
    transform: translate(5rem, 0rem);
    top: 47rem;
  }
}